/* eslint-disable */

@font-face {
	font-family: 'S-CoreDream-Thin';
	src: url('./font/SCDream-Thin.otf') format('opentype');
}
@font-face {
	font-family: 'S-CoreDream-Light';
	src: url('./font/SCDream-Light.otf') format('opentype');
}
@font-face {
	font-family: 'S-CoreDream-Medium';
	src: url('./font/SCDream-Medium.otf') format('opentype');
}
@font-face {
	font-family: 'S-CoreDream-Bold';
	src: url('./font/SCDream-Bold.otf') format('opentype');
}


html, body {
  padding: 0px;
  margin: 0px;
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  font-family: 'S-CoreDream-Thin';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.row {
  margin-right: 0px;
  margin-left: 0px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.footer {
  font-size: 12px;
  height: 92px;
  line-height: 24px;
  padding: 23px 0;
  background: #f1f1f1;
}

.main-img {
  width: 100%;
}