/* eslint-disable */

.App {
  text-align: center;
}

.header {
  font-family: 'S-CoreDream-Medium';
  height: 68px;
  padding-top: 30px;
  line-height: 38px;
  font-size: 20px;
}

.top-wrap {
  display: flex;
  position: absolute;
  right: 0;
  top: 30px;
}

.top-btn {
  width: 38px;
  height: 38px;
  line-height: 45px;
}

.top-img {
  height: 19px;
}

.category_wrap {
  height: 58px;
  width: 100%;
  overflow-x: auto;
}

.category_wrap ul {
  height: 100%;
  width: max-content;
}

.category_wrap li {
  height: 56px;
  border-bottom: 2px solid transparent;
  width: 98px;
  margin: 0px 3px;
  line-height: 58px;
  text-align: center;
  font-family: 'S-CoreDream-Medium';
  font-size: 14px;
  float: left;
  letter-spacing: 0.7px;
  color: #A2A2A2;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
